import { getStoreBuilder } from "vuex-typex";
import NetworkServices from '../../services/network.service';
import LoggerService from '../../services/logger.service';
var initialRolesState = {
    rolesList: []
};
var roleState = getStoreBuilder().module("roles", initialRolesState);
function getRoles() {
    NetworkServices.getRoles()
        .then(function (rolesData) { return roles.commitFillUpRolesList(rolesData); })
        .catch(function (err) { return LoggerService.error(err.message); });
}
function fillUpRolesList(state, roles) {
    state.rolesList = roles;
}
function logout(state) {
    Object.assign(state, initialRolesState);
}
var stateGetter = roleState.state();
var roles = {
    get state() { return stateGetter(); },
    commitFillUpRolesList: roleState.commit(fillUpRolesList, 'fillUpRolesList'),
    dispatchGetRoles: roleState.dispatch(getRoles),
    commitLogout: roleState.commit(logout, 'logout')
};
export default roles;



