import Vue from 'vue';
import Router from 'vue-router';
import { clearStoreAtLogout } from '../store';
import auth from '../store/modules/auth';
import TokenService from '../services/token.service';
import { Main, Login, Profile, Settings, Users, Register, Orders } from '../components/';
Vue.use(Router);
var main = {
    path: '/',
    name: 'Main',
    component: Main,
    meta: {
        localname: 'Main page'
    }
};
var login = {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
        localname: 'Login page'
    }
};
var profile = {
    path: '/profile/:id?',
    name: 'Profile',
    component: Profile,
    meta: {
        localname: 'Profile page'
    },
    props: true
};
var settings = {
    path: '/settings',
    name: 'Settings',
    component: Settings,
    meta: {
        localname: 'Settings page'
    }
};
var users = {
    path: '/users',
    name: 'Users',
    component: Users,
    meta: {
        localname: 'Users page'
    }
};
var register = {
    path: '/register',
    name: 'Register',
    component: Register,
    meta: {
        requiresRoles: ['admin'],
        localname: 'Register page'
    }
};
var orders = {
    path: '/orders',
    name: 'Orders',
    component: Orders,
    meta: {
        localname: 'Orders page'
    }
};
var routes = [
    main,
    login,
    profile,
    settings,
    users,
    register,
    orders
];
var router = new Router({
    routes: routes
});
router.beforeEach(function (to, from, next) {
    var isAuthorized = !!auth.state.user;
    if (!isAuthorized) {
        return to.name !== 'Login' ? next({ name: 'Login' }) : next();
    }
    if (isAuthorized) {
        return TokenService.checkLifetime()
            .then(function () {
            return to.name === 'Login' ? next({ name: 'Main' }) : checkRoles(to, from, next);
        })
            .catch(function (err) {
            clearStoreAtLogout();
            return next({ name: 'Login' });
        });
    }
});
function checkRoles(to, from, next) {
    if (!to.meta.requiresRoles)
        return next();
    var user = auth.state.user;
    var checkRole = function (role) { return to.meta.requiresRoles.includes(role); };
    user.roles.split(',').some(checkRole)
        ? next()
        : next({ name: 'Main' });
}
export default router;



