var isAdmin = function (user) { return user.roles.split(',').includes('admin'); };
var isUser = function (user) { return user.roles.split(',').includes('user'); };
var isVisitor = function (user) { return user.roles.split(',').includes('visitor'); };
export default { isAdmin: isAdmin, isUser: isUser, isVisitor: isVisitor };



